
import Vue from 'vue'
import { getQuestionList } from '@/api/detail.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: 'questions',
components: {
Tab,
TabItem,
},
data () {
return {
list:'',
productid:'',
skuid:'',
}
},
methods: {
listquestion(){
const postData = {
productid:this.productid,
skuid:this.skuid,
}
getQuestionList(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.list = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('请求出错！', 'middle')
})
},
godetail(id){
this.$router.push({path:"/orderDetail/questionsdetail",query:{'questionid':id,'productid':this.productid,'skuid':this.skuid}})
},
goanswer(id,text){
window.localStorage.setItem("questiontext",text)
this.$router.push({path:"/orderDetail/answer",query:{'questionid':id}})
},
goissue(){
this.$router.push({path:"/orderDetail/issue",query:{'productid':this.productid,'skuid':this.skuid}})
},
gotodetail(){
this.$router.push({path: '/orderDetail', query: {'productid': this.productid,'skuid': this.skuid}})
}
},
created () {
this.productid = this.$route.query.productid
this.skuid = this.$route.query.skuid
this.listquestion()
},
mounted(){
const that = this
Vue.wechat.ready(() => {
Vue.wechat.checkJsApi({
jsApiList: ['chooseImage', 'previewImage', 'uploadImage', 'downloadImage', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'scanQRCode'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function (res) {
console.log(res)
}
})

const shareid = localStorage.getItem('shareid')

Vue.wechat.onMenuShareTimeline({
title: "EDIFIER问答",
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questions?productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})

Vue.wechat.onMenuShareAppMessage({
title: "EDIFIER问答",
desc: '',
link: process.env.BASE_SELF+'/weixin/index.html?#/orderDetail/questions?productid='+that.productid+'&skuid='+that.skuid+'',
imgUrl: process.env.BASE_SELF+'/weixin/static/images/sharelogo.jpg',

success: function (res) {

}
})
})
}
}



